.wrap {
  position: fixed;
  top: 0;
  z-index: 1001;
  background: #FFFFFF;
}
.content {
  width: 100vw;
  height: 52px;
  padding: 0 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background: transparent;
  color: #7f8c8d;
}
.search-bar {
  width: 300px;
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  padding: 0 5px;
  --borderWidth: 1.5px;
  --borderRadius: 30px;
  background: #FFFFFF;
  position: relative;
  border-radius: var(--borderRadius);
}
.search-bar:after {
  content: '';
  position: absolute;
  top: calc(-1 * var(--borderWidth));
  left: calc(-1 * var(--borderWidth));
  height: calc(100% + var(--borderWidth) * 2);
  width: calc(100% + var(--borderWidth) * 2);
  background: -webkit-linear-gradient(30deg, #1DC4E9, #1DE9B6);
  background: -o-linear-gradient(30deg, #1DC4E9, #1DE9B6);
  background: linear-gradient(60deg, #1DC4E9, #1DE9B6);
  border-radius: calc(2 * var(--borderRadius));
  z-index: -1;
  background-size: 300% 300%;
}
@-webkit-keyframes animatedbordergradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes animatedbordergradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.menu p {
  margin: 0.1em;
  width: 5em;
  text-align: center;
  padding: 0.5em 0;
  font-size: 1.4em;
  border-radius: 1em;
  font-weight: 300;
}
.menu p.active {
  color: white;
  background: -webkit-linear-gradient(30deg, #1DC4E9, #1DE9B6);
  background: -o-linear-gradient(30deg, #1DC4E9, #1DE9B6);
  background: linear-gradient(60deg, #1DC4E9, #1DE9B6);
  -webkit-box-shadow: 1px 3px 20px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 3px 20px 2px rgba(0, 0, 0, 0.2);
}
.avatar {
  width: 40px;
  height: 40px;
  margin: 0 30px;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50px;
}
.avatar-onload {
  width: 40px;
  height: 40px;
  margin: 0 30px;
  background-size: cover;
  background: no-repeat 50% 50%;
  border-radius: 50px;
  background: #222222;
}
.board-inactive {
  -webkit-transition: all 0.6s;
  -o-transition: all 0.6s;
  transition: all 0.6s;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}
.board-active {
  -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
          transform: rotate(360deg);
}



